"use client";
import { motion, useScroll, useMotionValueEvent } from "framer-motion";
import Link from "next/link";
import { useState } from "react";

export default function NavbarTop() {
  const [hidden, setHidden] = useState(false);
  const { scrollY } = useScroll();

  useMotionValueEvent(scrollY, "change", (y) => {
    setHidden(y > 80);
  });

  const links = [
    { name: "Inicio", href: "#inicio" },
    { name: "Tecnolog√≠as", href: "#tecnologias" },
    { name: "Proyectos", href: "#proyectos" },
    { name: "Contacto", href: "#contacto" },
  ];

  return (
    <motion.nav
      initial={{ y: 0 }}
      animate={{ y: hidden ? -80 : 0 }}
      transition={{ duration: 0.4, ease: "easeOut" }}
      className="fixed top-0 left-0 w-full z-50 backdrop-blur-md bg-white/40 dark:bg-gray-900/40 border-b border-white/10"
    >
      <div className="max-w-6xl mx-auto flex justify-between items-center px-6 py-3">
        <Link href="#inicio" className="font-extrabold text-2xl bg-gradient-to-r from-cyan-400 to-purple-500 bg-clip-text text-transparent">
          PM.dev
        </Link>
        <div className="flex gap-6">
          {links.map((link) => (
            <a
              key={link.name}
              href={link.href}
              className="relative text-sm md:text-base text-gray-700 dark:text-gray-300 hover:text-cyan-400 transition"
            >
              {link.name}
              <span className="absolute bottom-0 left-0 w-0 h-[2px] bg-cyan-400 transition-all group-hover:w-full" />
            </a>
          ))}
        </div>
      </div>
    </motion.nav>
  );
}
